import React from 'react';
import classes from "./MainDisplay.module.css"
import serverLogo from "../../assets/img/serverLogo.png"
const MainDisplay = () => {



    return (
        <div className={classes.container}>
            <ul className={classes.serversList}>
                {/* <li className={classes.server}><img src={serverLogo} /></li> */}
            </ul>
            <div className={classes.channels}>

                {/*Server Information*/}
            </div>
            <div className={classes.channelContent}>
                {/*Channel content*/}
            </div>
            <div className={classes.members}>
                {/*Server members*/}
            </div>
        </div>
    )
}

export default MainDisplay;